    // ÎÇ†Ïßú Í≤ÄÏ¶ù Î∞è Ïù∏ÌÑ∞ÎùΩ Ìï®Ïàò
    function setupDateInterlock() {
        // ÎÇ©Í∏∞ ÎÇ†Ïßú Î≥ÄÍ≤Ω Ïãú Ïù¥Î≤§Ìä∏
        deliveryDate_orderRegisterPage_tab_1.addEventListener("change", () => {
            const deliveryDate = new Date(deliveryDate_orderRegisterPage_tab_1.value);
            if (isNaN(deliveryDate.getTime())) {
                // ÎÇ©Í∏∞ ÎÇ†ÏßúÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏúºÎ©¥ Ìè¨Ïû• ÎÇ†Ïßú ÎπÑÌôúÏÑ±Ìôî
                packDate_orderRegisterPage_tab_1.value = "";
                packDate_orderRegisterPage_tab_1.disabled = true;
                return;
            }
            // ÎÇ©Í∏∞ ÎÇ†ÏßúÍ∞Ä Ïú†Ìö®ÌïòÎ©¥ Ìè¨Ïû• ÎÇ†Ïßú ÌôúÏÑ±Ìôî
            packDate_orderRegisterPage_tab_1.readOnly = false;
            packDate_orderRegisterPage_tab_1.disabled = false;
        });
        // Ìè¨Ïû• ÎÇ†Ïßú Î≥ÄÍ≤Ω Ïãú Ïù¥Î≤§Ìä∏
        packDate_orderRegisterPage_tab_1.addEventListener("change", () => {
            const deliveryDate = new Date(deliveryDate_orderRegisterPage_tab_1.value);
            const packDate = new Date(packDate_orderRegisterPage_tab_1.value);
            if (isNaN(deliveryDate.getTime()) || isNaN(packDate.getTime())) {
                // ÎÇ†ÏßúÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏúºÎ©¥ ÏïÑÎ¨¥ ÏûëÏóÖÎèÑ ÌïòÏßÄ ÏïäÏùå
                return;
            }
            if (packDate > deliveryDate) {
                // Ìè¨Ïû• ÎÇ†ÏßúÍ∞Ä ÎÇ©Í∏∞ ÎÇ†ÏßúÎ≥¥Îã§ Îä¶ÏúºÎ©¥ Í≤ΩÍ≥† Î∞è Ï¥àÍ∏∞Ìôî
                ModalUtil.confirm({
                    title: "ÎÇ†Ïßú Ïò§Î•ò",
                    message: "Ìè¨Ïû•ÏòàÏ†ïÏùºÏùÄ ÎÇ©Í∏∞ÏùºÎ≥¥Îã§ Îä¶ÏùÑ Ïàò ÏóÜÏäµÎãàÎã§. Îã§Ïãú ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.",
                    type: "error"
                });
                packDate_orderRegisterPage_tab_1.value = "";
            }
        });
    }
    function handleFilterChange() {
        // ÌïÑÌÑ∞ Í∞í Í∞ÄÏ†∏Ïò§Í∏∞
        const selectedYear = filterYear_orderRegisterPage_tab_1.value; // Ïòà: "2025"
        const selectedEquipGroup = filterEquipGroup_orderRegisterPage_tab_1.value;
        const selectedClient = filterClient_orderRegisterPage_tab_1.value;
        console.log("üîç ÏÑ†ÌÉùÎêú ÌïÑÌÑ∞ Í∞í:", {
            year: selectedYear,
            equipGroup: selectedEquipGroup,
            client: selectedClient,
        });
        // ÌòÑÏû¨ Î†åÎçîÎßÅÎêú ÌÖåÏù¥Î∏î Îç∞Ïù¥ÌÑ∞ÏóêÏÑú ÌïÑÌÑ∞ÎßÅ
        const rows = Array.from(orderListBody_orderRegisterPage_tab_1.querySelectorAll("tr"));
        rows.forEach((row) => {
            const codeNo = row.querySelector("td:nth-child(1) button")?.getAttribute("data-number") || "";
            const equipName = row.querySelector("td:nth-child(2)")?.textContent || "";
            const clientName = row.querySelector("td:nth-child(3)")?.textContent || "";
            // Ï≤´ Î≤àÏß∏ ÏÖÄÏóêÏÑú 4, 5Î≤àÏß∏ Ïà´Ïûê Ï∂îÏ∂ú
            const yearSuffix = codeNo.substring(3, 5); // Ïòà: "ISS25-312S" -> "25"
            // ÏàòÏ£ºÎ≤àÌò∏ ÏÖÄÏóêÏÑú ÎßàÏßÄÎßâ Î¨∏Ïûê Ï∂îÏ∂ú (Eqtype)
            const Eqtype = codeNo.substring(codeNo.length - 1); // Ïòà: "ISS25-312S" -> "S" ÎòêÎäî "A"
            //Ïû•ÎπÑÍµ∞ 
            let matchesEqtype = false;
            if (selectedEquipGroup === "Ï†ÑÏ≤¥") {
                matchesEqtype = true; // "Ï†ÑÏ≤¥"Í∞Ä ÏÑ†ÌÉùÎêòÎ©¥ Î™®Îì† EqtypeÏùÑ ÌóàÏö©
            }
            else {
                if (Eqtype === "A" && selectedEquipGroup === "Wet") {
                    matchesEqtype = true;
                }
                if (Eqtype === "S" && selectedEquipGroup === "Single") {
                    matchesEqtype = true;
                }
            }
            // ÎÖÑÎèÑ
            let matchesYear = false;
            if (selectedYear === "Ï†ÑÏ≤¥") {
                matchesYear = true; // "Ï†ÑÏ≤¥"Í∞Ä ÏÑ†ÌÉùÎêòÎ©¥ Î™®Îì† ÎÖÑÎèÑÎ•º ÌóàÏö©
            }
            else {
                matchesYear = selectedYear === "" || yearSuffix === selectedYear.substring(2, 4); // "2025" -> "25"
            }
            // Í≥†Í∞ùÏÇ¨
            let matchesClient = false;
            if (selectedClient === "Ï†ÑÏ≤¥") {
                matchesClient = true; // "Ï†ÑÏ≤¥"Í∞Ä ÏÑ†ÌÉùÎêòÎ©¥ Î™®Îì† Í≥†Í∞ùÏÇ¨Î•º ÌóàÏö©
            }
            else {
                matchesClient = selectedClient === "" || clientName.includes(selectedClient);
            }
            // Ï°∞Í±¥Ïóê ÎßûÏúºÎ©¥ Î≥¥Ïù¥Í∏∞, ÏïÑÎãàÎ©¥ Ïà®Í∏∞Í∏∞
            if (matchesEqtype && matchesYear && matchesClient) {
                row.style.display = ""; // Î≥¥Ïù¥Í∏∞
            }
            else {
                row.style.display = "none"; // Ïà®Í∏∞Í∏∞
            }
        });
    }
