export const ModalUtil = {
    // ‚¨áÔ∏è ModalUtil.ts Îß® ÏïÑÎûòÏóê Î©îÏÑúÎìú ÌïòÎÇò Ï∂îÍ∞Ä (show/confirm ÏïÑÎûòÏóê)
    /**
     * Ïà´Ïûê ÏûÖÎ†• Î™®Îã¨ (ÌôïÏù∏/Ï∑®ÏÜå)
     * - ÏÇ¨Ïö© Ïòà: const n = await ModalUtil.promptNumber({ title:"Ï∂îÍ∞Ä", message:"Î≥ÄÍ≤Ω ÏàòÎüâÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî." });
     * - Ï∑®ÏÜå Ïãú null Î∞òÌôò
     */
    async promptNumber({ title = "ÏûÖÎ†•", message = "Í∞íÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.", defaultValue = 1, min = 1, max, type = "info", placeholder = "ÏàòÎüâ" }) {
        const el = this.ensureElement();
        const titleEl = el.querySelector("#modalTitle");
        const msgEl = el.querySelector("#modalMessage");
        const okBtn = el.querySelector("#modalCloseBtn");
        const cancelBtn = el.querySelector("#modalCancelBtn");
        this.setStyleByType(type);
        titleEl.textContent = title;
        // ÏûÖÎ†•Î∞ïÏä§ Ìè¨Ìï®Ìï¥ÏÑú Î©îÏãúÏßÄ Íµ¨ÏÑ±
        msgEl.innerHTML = `
      <div class="space-y-3">
        <div class="text-sm text-gray-600">${message}</div>
        <input id="modalPromptInput" type="number"
               class="w-full border rounded-lg px-3 py-2 text-center"
               value="${defaultValue}"
               ${min !== undefined ? `min="${min}"` : ""}
               ${max !== undefined ? `max="${max}"` : ""}
               placeholder="${placeholder}" />
      </div>
    `;
        cancelBtn.classList.remove("hidden");
        okBtn.textContent = "ÌôïÏù∏";
        cancelBtn.textContent = "Ï∑®ÏÜå";
        el.classList.remove("hidden");
        const input = () => el.querySelector("#modalPromptInput");
        return await new Promise((resolve) => {
            const onOk = () => {
                const v = Number((input().value || "").trim());
                if (!Number.isFinite(v) || (min !== undefined && v < min) || (max !== undefined && v > max)) {
                    // Í∞ÑÎã®Ìïú ÌîºÎìúÎ∞±
                    input().classList.add("ring-2", "ring-rose-500");
                    setTimeout(() => input().classList.remove("ring-2", "ring-rose-500"), 600);
                    input().focus();
                    return;
                }
                cleanup();
                this.hide();
                resolve(v);
            };
            const onCancel = () => {
                cleanup();
                this.hide();
                resolve(null);
            };
            const onKey = (ev) => {
                if (ev.key === "Escape")
                    onCancel();
                if (ev.key === "Enter")
                    onOk();
            };
            const cleanup = () => {
                okBtn.removeEventListener("click", onOk);
                cancelBtn.removeEventListener("click", onCancel);
                window.removeEventListener("keydown", onKey);
            };
            okBtn.addEventListener("click", onOk);
            cancelBtn.addEventListener("click", onCancel);
            window.addEventListener("keydown", onKey);
            // ÏûêÎèô Ìè¨Ïª§Ïä§
            setTimeout(() => input()?.focus(), 50);
        });
    },
    el: null,
    ensureElement() {
        if (this.el)
            return this.el;
        const div = document.createElement("div");
        div.id = "globalModalPopup";
        div.className =
            "hidden fixed inset-0 z-[9999] flex items-center justify-center bg-black/50";
        // Í∏∞Î≥∏ Íµ¨Ï°∞: ÏïÑÏù¥ÏΩò, ÌÅ∞ ÌÉÄÏù¥ÌãÄ, Î©îÏãúÏßÄ, Î≤ÑÌäºÎì§
        div.innerHTML = `
      <div id="modalBox" class="bg-white rounded-2xl shadow-2xl w-[380px] p-6 text-center transition-all">
        <div id="modalIcon" class="text-6xl mb-4 select-none">‚ÑπÔ∏è</div>
        <h2 id="modalTitle" class="text-2xl font-extrabold mb-3 text-gray-900 tracking-tight">ÏïåÎ¶º</h2>
        <p id="modalMessage" class="text-sm text-gray-600 mb-6 leading-6"></p>
        <div id="modalBtns" class="flex items-center justify-center gap-2">
          <button id="modalCancelBtn"
            class="px-4 py-2 rounded-lg border border-gray-200 text-gray-700 hover:bg-gray-50">Ï∑®ÏÜå</button>
          <button id="modalCloseBtn"
            class="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">ÌôïÏù∏</button>
        </div>
      </div>
    `;
        document.body.appendChild(div);
        this.el = div;
        return div;
    },
    // ÎÇ¥Î∂Ä Í≥µÌÜµ Ïä§ÌÉÄÏùºÎßÅ
    setStyleByType(type) {
        const el = this.ensureElement();
        const iconEl = el.querySelector("#modalIcon");
        const titleEl = el.querySelector("#modalTitle");
        // Í∏∞Î≥∏Í∞í
        let icon = "‚ÑπÔ∏è";
        let iconClass = "text-6xl text-blue-500 mb-4";
        let titleClass = "text-2xl font-extrabold mb-3 text-gray-900 tracking-tight";
        switch (type) {
            case "success":
                icon = "‚úÖ";
                iconClass = "text-6xl text-green-500 mb-4";
                break;
            case "error":
                icon = "‚ùå";
                iconClass = "text-6xl text-red-500 mb-4";
                break;
            case "warning":
                icon = "‚ö†Ô∏è";
                iconClass = "text-6xl text-yellow-500 mb-4";
                break;
            case "increase":
                icon = "‚ûï";
                iconClass = "text-6xl text-emerald-600 mb-4";
                titleClass = "text-3xl font-black mb-3 text-emerald-700 tracking-tight";
                break;
            case "decrease":
                icon = "‚ûñ";
                iconClass = "text-6xl text-rose-600 mb-4";
                titleClass = "text-3xl font-black mb-3 text-rose-700 tracking-tight";
                break;
            default:
                break;
        }
        iconEl.textContent = icon;
        iconEl.className = iconClass;
        titleEl.className = titleClass;
    },
    /**
     * (Îã®Ïàú) ÏïåÎ¶º Î™®Îã¨
     * @param message Î≥∏Î¨∏ Î©îÏãúÏßÄ
     * @param title Ï†úÎ™©
     * @param type "info" | "success" | "error" | "increase" | "decrease" | "warning"
     */
    show(message, title = "ÏïåÎ¶º", type = "info") {
        const el = this.ensureElement();
        const titleEl = el.querySelector("#modalTitle");
        const msgEl = el.querySelector("#modalMessage");
        const okBtn = el.querySelector("#modalCloseBtn");
        const cancelBtn = el.querySelector("#modalCancelBtn");
        const btnWrap = el.querySelector("#modalBtns");
        this.setStyleByType(type);
        titleEl.textContent = title;
        msgEl.textContent = message;
        // Îã®Ïàú ÏïåÎ¶º ‚Üí Ï∑®ÏÜå Î≤ÑÌäº Ïà®ÍπÄ, ÌôïÏù∏Îßå
        cancelBtn.classList.add("hidden");
        okBtn.textContent = "Îã´Í∏∞";
        el.classList.remove("hidden");
        const close = () => {
            this.hide();
            okBtn.removeEventListener("click", close);
        };
        okBtn.addEventListener("click", close);
    },
    /**
     * ÌôïÏù∏/Ï∑®ÏÜå Î™®Îã¨ (Promise Î∞òÌôò)
     * - "Ï∂îÍ∞Ä" / "Í∞êÏÜå" Îì±Ïùò ÍµµÏùÄ ÌÉÄÏù¥ÌãÄÏùÑ ÌÅ¨Í≤å ÌëúÏãú Í∞ÄÎä•
     */
    confirm({ title = "ÌôïÏù∏", message = "", confirmText = "ÌôïÏù∏", cancelText = "Ï∑®ÏÜå", type = "warning", }) {
        const el = this.ensureElement();
        const titleEl = el.querySelector("#modalTitle");
        const msgEl = el.querySelector("#modalMessage");
        const okBtn = el.querySelector("#modalCloseBtn");
        const cancelBtn = el.querySelector("#modalCancelBtn");
        this.setStyleByType(type);
        titleEl.textContent = title; // ‚Üê "Ï∂îÍ∞Ä" ÎòêÎäî "Í∞êÏÜå" ÌÅ¨Í≤å ÌëúÏãú
        msgEl.textContent = message;
        cancelBtn.textContent = cancelText;
        okBtn.textContent = confirmText;
        cancelBtn.classList.remove("hidden");
        el.classList.remove("hidden");
        return new Promise((resolve) => {
            const onOk = () => {
                cleanup();
                this.hide();
                resolve(true);
            };
            const onCancel = () => {
                cleanup();
                this.hide();
                resolve(false);
            };
            const onKey = (ev) => {
                if (ev.key === "Escape")
                    onCancel();
                if (ev.key === "Enter")
                    onOk();
            };
            const cleanup = () => {
                okBtn.removeEventListener("click", onOk);
                cancelBtn.removeEventListener("click", onCancel);
                window.removeEventListener("keydown", onKey);
            };
            okBtn.addEventListener("click", onOk);
            cancelBtn.addEventListener("click", onCancel);
            window.addEventListener("keydown", onKey);
        });
    },
    hide() {
        const el = this.ensureElement();
        el.classList.add("hidden");
    },
};
