import { ModalUtil } from "../workspace/utils/ModalUtil";
let initOrderRegister_tab_3_init = false;
export function initOrderRegister_tab_3(API_BASE) {
    const container = document.getElementById("orderRegisterPage_tab_3");
    if (!container) {
        console.error("Container with id 'orderRegisterPage_tab_3' not found.");
        return;
    }
    // ÏàòÏßëÎêú DOM ÏöîÏÜåÎ•º Ï†ÄÏû•Ìï† Í∞ùÏ≤¥
    const domElements = {};
    // container ÎÇ¥Î∂ÄÏùò Î™®Îì† idÎ•º Í∞ÄÏßÑ ÏöîÏÜåÎ•º ÏàòÏßë
    const elementsWithId = container.querySelectorAll("[id]");
    elementsWithId.forEach((element) => {
        const id = element.id;
        domElements[id] = element; // idÎ•º ÌÇ§Î°ú, DOM ÏöîÏÜåÎ•º Í∞íÏúºÎ°ú Ï†ÄÏû•
    });
    Object.keys(domElements).forEach((id) => {
        window[id] = domElements[id]; // Ï†ÑÏó≠ Î≥ÄÏàòÎ°ú Îì±Î°ù
    });
    //Ïù¥Î≤§Ìä∏ Ï§ëÎ≥µ Î∞©ÏßÄÏûÑ
    if (initOrderRegister_tab_3_init) {
        return;
    }
    initOrderRegister_tab_3_init = true;
    //#region Í∞ÅÏ¢Ö Ìï®Ïàò Î™®ÏùåÏßë
    function AllClear() {
        const leftBody = document.getElementById('left_body');
        // ÏÉÅÎã® Í∏∞Î≥∏ Ï†ïÎ≥¥ Ï¥àÍ∏∞Ìôî
        domElements.specOrderNo_orderRegisterPage_tab_3.value = "";
        domElements.specOrderName_orderRegisterPage_tab_3.value = "";
        domElements.specOrderClient_orderRegisterPage_tab_3.value = "";
        domElements.specOrderNo_orderRegisterPage_tab_3.classList.remove("bg-gray-300");
        domElements.specOrderName_orderRegisterPage_tab_3.classList.remove("bg-gray-300");
        domElements.specOrderClient_orderRegisterPage_tab_3.classList.remove("bg-gray-300");
        const canvas = document.getElementById('calendar_canvas');
        const ctx = canvas.getContext('2d');
        console.log("Î™®Îì† ÌïÑÎìúÏôÄ Ï∫îÎ≤ÑÏä§ Îç∞Ïù¥ÌÑ∞Í∞Ä Ï¥àÍ∏∞ÌôîÎêòÏóàÏäµÎãàÎã§.");
    }
    //#endregion
    //#region ÌòÑÏû¨ Îì±Î°ù ÎêòÏñ¥ÏûàÎäî ÏàòÏ£ºÍ±¥ Î∂àÎü¨Ïò§Í∏∞
    async function fetchAndRenderOrderList() {
        showProgressModal("ÌôîÎ©¥ Î°úÎî©Ï§ë ...");
        updateProgressBar(10);
        await new Promise(resolve => setTimeout(resolve, 300)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
        updateProgressBar(50);
        await new Promise(resolve => setTimeout(resolve, 200)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
        try {
            const response = await fetch(`${API_BASE}/api/innomax-projects`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                },
            });
            if (!response.ok) {
                throw new Error("Failed to fetch orders");
            }
            const data = await response.json();
            let orders = data.data;
            // ÏΩîÎìúÎ≤àÌò∏ Í∏∞Ï§Ä Ï†ïÎ†¨ (- Ïù¥ÌõÑÏùò 3Í∏ÄÏûêÎ•º Í∏∞Ï§ÄÏúºÎ°ú)
            orders = orders.sort((a, b) => {
                const aNumber = parseInt(a.code_no.split("-")[1]?.substring(0, 3) || "0", 10);
                const bNumber = parseInt(b.code_no.split("-")[1]?.substring(0, 3) || "0", 10);
                return aNumber - bNumber;
            });
            // ÌÖåÏù¥Î∏î Î∞îÎîî Ï¥àÍ∏∞Ìôî
            domElements.orderListBody_orderList_Modal_orderRegisterPage_tab_3.innerHTML = "";
            // Í∞Å ÏàòÏ£ºÍ±¥ÏùÑ ÌÖåÏù¥Î∏îÏóê Ï∂îÍ∞Ä
            orders.forEach((order, index) => {
                const detail = order.detail_json;
                const row = document.createElement("tr");
                // Í≤©Ï§Ñ Ïä§ÌÉÄÏùºÎßÅ: Í∏∞Î≥∏ÏÉâÍ≥º ÏòÖÏùÄ ÌïòÎäòÏÉâ Î≤àÍ∞àÏïÑÍ∞ÄÎ©∞ Ï†ÅÏö©
                const rowStyle = index % 2 === 0 ? "bg-white" : "bg-blue-200";
                row.innerHTML = `
                <td class="border px-3 py-2 text-center ${rowStyle}">
                    <button class="bg-white-200 text-black px-2 py-1 rounded hover:bg-white-200" 
                    data-action="code_no_button"
                    data-number="${order.code_no}">
                        ${order.code_no}
                    </button>
                </td>
                <td class="border px-3 py-2 text-center ${rowStyle}">${detail.equipName}</td>
                <td class="border px-3 py-2 text-center ${rowStyle}">${detail.clientName}</td>
            `;
                domElements.orderListBody_orderList_Modal_orderRegisterPage_tab_3.appendChild(row);
            });
        }
        catch (error) {
            console.error("Error fetching orders:", error);
        }
        bindRowEvents();
        //visible_option("init");
        updateProgressBar(70);
        await new Promise(resolve => setTimeout(resolve, 100)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
        updateProgressBar(100);
        hideProgressModal();
    }
    //#endregion
    //#region ÌòÑÏû¨ ÏàòÏ£ºÍ±¥ ÌÖåÏù¥Î∏îÏóêÏÑú Î≤ÑÌäº ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏Ï™Ω  
    function bindRowEvents() {
        domElements.orderListBody_orderList_Modal_orderRegisterPage_tab_3.querySelectorAll("button").forEach((btn) => {
            btn.addEventListener("click", async (e) => {
                const target = e.currentTarget;
                const action = target.dataset.action;
                const number = target.dataset.number;
                if (!action || !number)
                    return;
                if (action === "code_no_button") {
                    console.log(`[order_registerPage_tab_1] ÏàòÏ£ºÎ≤àÌò∏ ÌÅ¥Î¶≠: ${number}`);
                    await ModalUtil.confirm({
                        title: "ÏàòÏ£ºÍ±¥ Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞",
                        message: `ÏàòÏ£ºÎ≤àÌò∏ ${number} Ïùò Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÏãúÍ≤†ÏäµÎãàÍπå?`,
                        type: "info"
                    });
                    showProgressModal("ÏàòÏ£ºÍ±¥ Î∂àÎü¨Ïò§Îäî Ï§ë...");
                    updateProgressBar(10);
                    await new Promise(resolve => setTimeout(resolve, 500)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
                    //Ìï¥Îãπ ÏàòÏ£ºÍ±¥ Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞
                    try {
                        const response = await fetch(`${API_BASE}/api/innomax-projects/target/${number}`, {
                            method: "GET",
                            headers: {
                                "Content-Type": "application/json",
                            },
                        });
                        if (!response.ok) {
                            throw new Error("Failed to fetch order details");
                        }
                        const data = await response.json();
                        const order = data.data;
                        const detail = order.detail_json;
                        //Î∂àÎü¨Ïò® ÏàòÏ£ºÍ±¥ Ï†ïÎ≥¥Î°ú ÏûÖÎ†•Ìèº Ï±ÑÏö∞Í∏∞
                        domElements.specOrderNo_orderRegisterPage_tab_3.value = order.code_no;
                        domElements.specOrderName_orderRegisterPage_tab_3.value = detail.equipName;
                        domElements.specOrderClient_orderRegisterPage_tab_3.value = detail.clientName;
                        domElements.specOrderNo_orderRegisterPage_tab_3.classList.add("bg-gray-300");
                        domElements.specOrderName_orderRegisterPage_tab_3.classList.add("bg-gray-300");
                        domElements.specOrderClient_orderRegisterPage_tab_3.classList.add("bg-gray-300");
                    }
                    catch (error) {
                        console.error("Error fetching order details:", error);
                        alert("Error fetching order details. Please try again. Í∞úÎ∞úÏûê Î¨∏Ïùò!");
                    }
                    updateProgressBar(50);
                    await new Promise(resolve => setTimeout(resolve, 200)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
                    updateProgressBar(100);
                    await new Promise(resolve => setTimeout(resolve, 100)); // ÏôÑÎ£å ÌõÑ ÏßÄÏó∞
                    hideProgressModal();
                    drawDhtmlxGantt("SINGLE", new Date("2026-01-01"), new Date("2026-03-20"));
                }
            });
        });
    }
    //#endregion
    //#region ÏùºÎã® Í∞ÑÌä∏Ï∞®Ìä∏Î∂ÄÌÑ∞ Í∑∏Î†§Î≤ÑÎ†§ Ïã±Í∏Ä, Ïõª 2Í∞úÎã§ 
    function drawDhtmlxGantt(eqType, projectStart, projectEnd) {
        let taskData = [];
        if (eqType === "SINGLE") {
            taskData = [
                { name: "ÏàòÏ£ºÎ≥¥Í≥†ÏÑú Î∞úÌñâ", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "1Ï∞® Kick up", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "2Ï∞® Kick up", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "Layout", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "PnID", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "Ïã§ÌñâÏòàÏÇ∞", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "MAIN BODY(EFEM, BUFFER BODY Ìè¨Ìï®)", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "LOCAL UNIT BODY", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "OP PANEL", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "MAIN MONITOR BRACKET", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "RINSE Î∞è PROCESS CHAMBER", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "CHAMBER UP-DOWN", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "CHAMBER EXHAUST", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "CHAMBER BASE", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "RINSE Î∞è PROCESS CHAMBER BOX", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "SHUTTER", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "INNER CUP", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "CHAMBER BOX SENSOR", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "RINSE Î∞è PROCESS DISPENSER", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "SPIN CHUCK", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "SPINDLE", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "Í≥†Ïïï REGULATOR", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "ROBOT ASSY", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "TANK", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "BUFFER", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "SERIAL NAME PLATE", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "ÏÜåÏùåÍ∏∞ BOX", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "MAIN BODY Î∞∞Í¥ÄÏÑ§Í≥Ñ", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "LOCAL Î∞∞Í¥Ä ÏÑ§Í≥Ñ", group: "ÏÑ§Í≥Ñ/Ï§ÄÎπÑ" },
                { name: "Main Body ÏûÖÍ≥†", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "Local unit ÏûÖÍ≥†", group: "Î∂ÄÌíàÏûÖÍ≥†" },
                { name: "Chamber base Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Dispenser Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Chamber up-down Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Spindle Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Chamber exhaust Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Chamber Cup Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Spin chuck Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Chamber box Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Shutter Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "Main Piping Î∞è Air Line Piping", group: "Î∞∞Í¥Ä/Î∞∞ÏÑ†" },
                { name: "Local Unit Piping Î∞è Air Line Piping", group: "Î∞∞Í¥Ä/Î∞∞ÏÑ†" },
                { name: "Programming", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "Wiring", group: "Î∞∞Í¥Ä/Î∞∞ÏÑ†" },
                { name: "ATM Setting", group: "Ï°∞Î¶Ω/ÏÖãÌåÖ" },
                { name: "ATM Teaching", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "IO Check", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "Manual Test", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "Auto Running Test", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "ÏÇ¨ÎÇ¥ QC", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "Inspection", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "PACKING", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" },
                { name: "Ï∂úÌïò", group: "ÌÖåÏä§Ìä∏/Ï∂úÌïò" }
            ];
        }
        if (taskData.length === 0)
            return;
        // 1) DHTMLX Gantt Îç∞Ïù¥ÌÑ∞/ÎßÅÌÅ¨ Íµ¨ÏÑ±
        let currentDate = new Date(projectStart);
        const tasks = [];
        const links = [];
        taskData.forEach((item, index) => {
            const id = index + 1;
            const start = new Date(currentDate);
            const duration = 0; // 0Ïùº
            const startStr = start.toISOString().substring(0, 10); // YYYY-MM-DD
            tasks.push({
                id,
                text: item.name,
                start_date: startStr,
                duration,
                progress: 0,
                group: item.group
            });
            if (index > 0) {
                links.push({
                    id: index,
                    source: index,
                    target: id,
                    type: "0"
                });
            }
            currentDate.setDate(currentDate.getDate() + 2);
        });
        const data = { data: tasks, links };
        const ganttDiv = document.getElementById('gantt_here');
        if (!ganttDiv) {
            console.error('#gantt_here ÏöîÏÜåÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.');
            return;
        }
        // Í∏∞Í∞Ñ Ï†úÌïú
        gantt.config.start_date = new Date(projectStart);
        gantt.config.end_date = new Date(projectEnd);
        // Ìè¨Îß∑
        gantt.config.date_format = "%Y-%m-%d";
        gantt.templates.date_grid = function (date) {
            const y = date.getFullYear();
            const m = date.getMonth() + 1;
            const d = date.getDate();
            return `${y}ÎÖÑ ${m}Ïõî ${d}Ïùº`;
        };
        // Ïª¨Îüº (Í∞ÄÎ°ú Ìè≠ ÏïΩÍ∞Ñ Ï§ÑÏûÑ)
        gantt.config.columns = [
            { name: "text", label: "ÏûëÏóÖ", width: 300, tree: true },
            { name: "group", label: "Í∑∏Î£π", align: "center", width: 110 },
            {
                name: "start_date",
                label: "ÏãúÏûëÏùº",
                align: "center",
                width: 130,
                template: function (task) {
                    const d = task.start_date instanceof Date
                        ? task.start_date
                        : gantt.date.parseDate(task.start_date, gantt.config.date_format);
                    const y = d.getFullYear();
                    const m = d.getMonth() + 1;
                    const day = d.getDate();
                    return `${y}ÎÖÑ ${m}Ïõî ${day}Ïùº`;
                }
            },
            { name: "duration", label: "Í∏∞Í∞Ñ(Ïùº)", align: "center", width: 70 }
        ];
        // Ïä§ÏºÄÏùº (day Ìè≠ Ï§ÑÏù¥Í∏∞ ÏúÑÌï¥ min_column_width Ï°∞Ï†ï)
        gantt.config.scales = [
            {
                unit: "month",
                step: 1,
                format: function (date) {
                    const y = date.getFullYear();
                    const m = date.getMonth() + 1;
                    return `${y}ÎÖÑ ${m}Ïõî`;
                }
            },
            {
                unit: "day",
                step: 1,
                format: function (date) {
                    const d = date.getDate();
                    return `${d}`; // ÏùºÎßå ÌëúÏãúÌï¥ÏÑú Îçî Ïª¥Ìå©Ìä∏ÌïòÍ≤å
                }
            }
        ];
        // ÌïòÎ£® ÏÖÄ Í∞ÄÎ°úÌè≠ ÏµúÏÜåÍ∞í (Í∏∞Î≥∏Î≥¥Îã§ ÏûëÍ≤å)
        gantt.config.min_column_width = 40; // ÌïÑÏöîÌïòÎ©¥ 30 ~ 50 ÏÇ¨Ïù¥Î°ú Ï°∞Ï†à
        // Ï§ÑÎ¨¥Îä¨
        gantt.templates.grid_row_class = function (_start, _end, row) {
            return row.id % 2 === 0 ? "gantt_row_even" : "gantt_row_odd";
        };
        gantt.templates.timeline_cell_class = function (_task, date) {
            const day = date.getDate();
            // ÌôÄÏßù Î∞∞Í≤Ω + border ÌÅ¥ÎûòÏä§Î•º Í∞ôÏù¥ Ï§å
            return (day % 2 === 0 ? "gantt_cell_even" : "gantt_cell_odd") + " gantt_cell_bordered";
        };
        gantt.init("gantt_here");
        gantt.clearAll();
        gantt.parse(data);
    }
    //#endregion
    //#region Ïù¥Î≤§Ìä∏ Í¥ÄÎ†® Î™®ÏùåÏßë
    domElements.readOrder_orderRegisterPage_tab_3.addEventListener("click", async () => {
        console.log("Read Order button clicked in Tab 3");
        await fetchAndRenderOrderList();
        domElements.modalOverlay_orderRegisterPage_tab_3.classList.remove("hidden");
    });
    domElements.closeModalBtn1_orderList_Modal_orderRegisterPage_tab_3.addEventListener("click", () => {
        domElements.modalOverlay_orderRegisterPage_tab_3.classList.add("hidden");
    });
    domElements.closeModalBtn2_orderList_Modal_orderRegisterPage_tab_3.addEventListener("click", () => {
        domElements.modalOverlay_orderRegisterPage_tab_3.classList.add("hidden");
    });
    domElements.pmsSave_orderRegisterPage_tab_3.addEventListener("click", async () => {
        const specOrderNo_orderRegisterPage_tab_3 = domElements.specOrderNo_orderRegisterPage_tab_3.value;
    });
    domElements.init_orderRegisterPage_tab_3.addEventListener("click", () => {
        AllClear();
    });
    //#endregion Ïù¥Î≤§Ìä∏ Í¥ÄÎ†® Î™®ÏùåÏßë
    //#region ÌîÑÎ°úÍ∑∏Î†àÏä§Î∞î Í¥ÄÎ†® Í±¥ÎìúÌïÑÏöîÏóÜÏùå
    function showProgressModal(message = "Ïû†ÏãúÎßå Í∏∞Îã§Î†§Ï£ºÏÑ∏Ïöî.") {
        const progressModal = document.getElementById("progressModal_orderRegisterPage");
        const progressBar = document.getElementById("progressBar_orderRegisterPage");
        const progressMessage = document.getElementById("progressMessage_orderRegisterPage");
        if (progressModal && progressBar && progressMessage) {
            progressMessage.textContent = message;
            progressBar.style.width = "0%"; // Ï¥àÍ∏∞Ìôî
            progressModal.classList.remove("hidden");
        }
    }
    function hideProgressModal() {
        const progressModal = document.getElementById("progressModal_orderRegisterPage");
        if (progressModal) {
            progressModal.classList.add("hidden");
        }
        const modalOverlay_orderRegisterPage_tab_3 = document.getElementById("modalOverlay_orderRegisterPage_tab_3");
        if (modalOverlay_orderRegisterPage_tab_3) {
            modalOverlay_orderRegisterPage_tab_3.classList.add("hidden");
        }
    }
    function updateProgressBar(percentage) {
        const progressBar = document.getElementById("progressBar_orderRegisterPage");
        if (progressBar) {
            progressBar.style.width = `${percentage}%`;
        }
    }
    //#endregion
}
