export function initUserRegisterPanel(API_BASE) {
    const userTableBody = document.getElementById("userTableBody");
    const userCount = document.getElementById("userCount");
    const userForm = document.getElementById("userForm");
    const modalMode = document.getElementById("modalMode");
    const modalNo = document.getElementById("modalNo");
    const userModal = document.getElementById("userModal");
    const permPreview = document.getElementById("permPreview");
    const permLabels = {
        order_register: "ÏàòÏ£ºÍ±¥Îì±Î°ù",
        task_assign: "ÏóÖÎ¨¥Ìï†Îãπ",
        progress: "ÏßÑÌñâÏÉÅÌô©",
        report: "ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†",
        request: "ÏöîÏ≤≠ÏÇ¨Ìï≠",
    };
    const permValues = {
        ReadWrite: "ÏùΩÍ≥† Ïì∞Í∏∞ Í∞ÄÎä•",
        ReadOnly: "ÏùΩÍ∏∞ Ï†ÑÏö©",
        NoAccess: "Ï†ëÍ∑º Î∂àÍ∞Ä",
    };
    function parsePerm(json) {
        try {
            const obj = json ? JSON.parse(json) : {};
            return {
                order_register: obj.order_register ?? "NoAccess",
                task_assign: obj.task_assign ?? "NoAccess",
                progress: obj.progress ?? "NoAccess",
                report: obj.report ?? "NoAccess",
                request: obj.request ?? "NoAccess",
            };
        }
        catch {
            return {
                order_register: "NoAccess",
                task_assign: "NoAccess",
                progress: "NoAccess",
                report: "NoAccess",
                request: "NoAccess",
            };
        }
    }
    function updatePermPreview(permissions) {
        if (!permPreview)
            return;
        const html = Object.entries(permissions)
            .map(([k, v]) => `${permLabels[k]} : ${permValues[v]}`)
            .join("<br>");
        permPreview.innerHTML = html;
    }
    // üü¶ ÏÇ¨Ïö©Ïûê Î™©Î°ù Î†åÎçîÎßÅ
    async function renderUsers() {
        try {
            const res = await fetch(`${API_BASE}/api/users`);
            const users = await res.json();
            userTableBody.innerHTML = "";
            users.forEach((u, idx) => {
                const p = parsePerm(u.permissions);
                const permText = Object.entries(p)
                    .map(([k, v]) => `${permLabels[k]} : ${permValues[v]}`)
                    .join("<br>");
                userTableBody.innerHTML += `
          <tr>
            <td class="px-4 py-2">${idx + 1}</td>
            <td class="px-4 py-2">${u.Name ?? "-"}</td>
            <td class="px-4 py-2">${u.ID}</td>
            <td class="px-4 py-2">****</td>
            <td class="px-4 py-2">${u.email ?? "-"}</td>
            <td class="px-4 py-2">${u.company_part ?? "-"}</td>
            <td class="px-4 py-2 text-xs">${permText}</td>
            <td class="px-4 py-2 text-center space-x-2">
              <button data-action="edit" data-no="${u.No}" class="px-3 py-1 bg-yellow-400 text-white rounded text-xs">ÏàòÏ†ï</button>
              <button data-action="delete" data-no="${u.No}" class="px-3 py-1 bg-red-500 text-white rounded text-xs">ÏÇ≠Ï†ú</button>
            </td>
          </tr>`;
            });
            userCount.innerText = `${users.length}Î™Ö`;
        }
        catch (err) {
            console.error("‚ùå ÏÇ¨Ïö©Ïûê Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", err);
        }
    }
    // üü¶ Î™®Îã¨ Ïó¥Í∏∞
    async function openUserModal(mode, no) {
        const title = document.getElementById("modalTitle");
        const nameInput = document.getElementById("modalName");
        const idInput = document.getElementById("modalID");
        const passwordInput = document.getElementById("modalPassword");
        const emailInput = document.getElementById("modalEmail");
        const companyInput = document.getElementById("modalCompanyPart");
        const Select = (id) => document.getElementById(id);
        passwordInput.type = "password";
        // ==============================
        // Ïã†Í∑ú ÏÇ¨Ïö©Ïûê Ï∂îÍ∞Ä
        // ==============================
        if (mode === "add") {
            title.innerText = "Ïã†Í∑ú ÏÇ¨Ïö©Ïûê Ï∂îÍ∞Ä";
            modalMode.value = "add";
            modalNo.value = "";
            userForm.reset();
            const defaultPerm = {
                order_register: "ReadWrite",
                task_assign: "ReadWrite",
                progress: "ReadWrite",
                report: "ReadWrite",
                request: "ReadWrite",
            };
            Select("ÏàòÏ£ºÍ±¥Îì±Î°ù").value = defaultPerm.order_register;
            Select("ÏóÖÎ¨¥Ìï†Îãπ").value = defaultPerm.task_assign;
            Select("ÏßÑÌñâÏÉÅÌô©").value = defaultPerm.progress;
            Select("ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†").value = defaultPerm.report;
            Select("ÏöîÏ≤≠ÏÇ¨Ìï≠").value = defaultPerm.request;
            updatePermPreview(defaultPerm);
        }
        // ==============================
        // ÏÇ¨Ïö©Ïûê ÏàòÏ†ï
        // ==============================
        else if (mode === "edit" && no) {
            try {
                const res = await fetch(`${API_BASE}/api/users/${no}`);
                const u = await res.json();
                title.innerText = "ÏÇ¨Ïö©Ïûê ÏàòÏ†ï";
                modalMode.value = "edit";
                modalNo.value = u.No;
                nameInput.value = u.Name ?? "";
                idInput.value = u.ID;
                passwordInput.value = "";
                passwordInput.placeholder = "Î≥ÄÍ≤Ω ÏãúÏóêÎßå ÏûÖÎ†•";
                emailInput.value = u.email ?? "";
                companyInput.value = u.company_part ?? "";
                const p = parsePerm(u.permissions);
                Select("ÏàòÏ£ºÍ±¥Îì±Î°ù").value = p.order_register;
                Select("ÏóÖÎ¨¥Ìï†Îãπ").value = p.task_assign;
                Select("ÏßÑÌñâÏÉÅÌô©").value = p.progress;
                Select("ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†").value = p.report;
                Select("ÏöîÏ≤≠ÏÇ¨Ìï≠").value = p.request;
                updatePermPreview(p);
            }
            catch (err) {
                console.error("‚ùå ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", err);
            }
        }
        userModal.classList.remove("hidden");
    }
    // üü¶ Î™®Îã¨ Îã´Í∏∞
    function closeUserModal() {
        userModal.classList.add("hidden");
    }
    window.togglePassword = function () {
        const input = document.getElementById("modalPassword");
        input.type = input.type === "password" ? "text" : "password";
    };
    // ÏÉà Í∂åÌïú ID
    const permIds = ["ÏàòÏ£ºÍ±¥Îì±Î°ù", "ÏóÖÎ¨¥Ìï†Îãπ", "ÏßÑÌñâÏÉÅÌô©", "ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†", "ÏöîÏ≤≠ÏÇ¨Ìï≠"];
    // üü¶ Í∂åÌïú select Î≥ÄÍ≤Ω ‚Üí ÎØ∏Î¶¨Î≥¥Í∏∞ Í∞±Ïã†
    permIds.forEach((id) => {
        const el = document.getElementById(id);
        if (el) {
            el.addEventListener("change", () => {
                const p = {
                    order_register: document.getElementById("ÏàòÏ£ºÍ±¥Îì±Î°ù").value,
                    task_assign: document.getElementById("ÏóÖÎ¨¥Ìï†Îãπ").value,
                    progress: document.getElementById("ÏßÑÌñâÏÉÅÌô©").value,
                    report: document.getElementById("ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†").value,
                    request: document.getElementById("ÏöîÏ≤≠ÏÇ¨Ìï≠").value,
                };
                updatePermPreview(p);
            });
        }
    });
    // üü¶ Ï†ÄÏû•
    if (userForm) {
        userForm.addEventListener("submit", async (e) => {
            e.preventDefault();
            const mode = modalMode.value;
            const no = modalNo.value || undefined;
            const Name = document.getElementById("modalName").value.trim();
            const ID = document.getElementById("modalID").value.trim();
            const password = document.getElementById("modalPassword").value.trim();
            const email = document.getElementById("modalEmail").value.trim() || null;
            const company_part = document.getElementById("modalCompanyPart").value.trim() || null;
            const permissions = {
                order_register: document.getElementById("ÏàòÏ£ºÍ±¥Îì±Î°ù").value,
                task_assign: document.getElementById("ÏóÖÎ¨¥Ìï†Îãπ").value,
                progress: document.getElementById("ÏßÑÌñâÏÉÅÌô©").value,
                report: document.getElementById("ÏßÑÌñâÏÉÅÌô©Î≥¥Í≥†").value,
                request: document.getElementById("ÏöîÏ≤≠ÏÇ¨Ìï≠").value,
            };
            try {
                if (mode === "add") {
                    await fetch(`${API_BASE}/api/users`, {
                        method: "POST",
                        headers: { "Content-Type": "application/json" },
                        body: JSON.stringify({ Name, ID, password, email, company_part, permissions }),
                    });
                }
                else {
                    const payload = { Name, ID, email, company_part, permissions };
                    if (password)
                        payload.password = password;
                    await fetch(`${API_BASE}/api/users/${no}`, {
                        method: "PUT",
                        headers: { "Content-Type": "application/json" },
                        body: JSON.stringify(payload),
                    });
                }
                await renderUsers();
                closeUserModal();
            }
            catch (err) {
                console.error("‚ùå ÏÇ¨Ïö©Ïûê Ï†ÄÏû• Ïã§Ìå®:", err);
            }
        });
    }
    // üü¶ ÏÇ≠Ï†ú
    async function deleteUser(no) {
        await fetch(`${API_BASE}/api/users/${no}`, { method: "DELETE" });
        await renderUsers();
    }
    userTableBody.addEventListener("click", (e) => {
        const target = e.target;
        if (target.dataset.action === "edit")
            openUserModal("edit", target.dataset.no);
        if (target.dataset.action === "delete")
            deleteUser(target.dataset.no);
    });
    document.addEventListener("keydown", (e) => {
        if (e.key === "Escape")
            closeUserModal();
    });
    window.openAddUserModal = () => openUserModal("add");
    window.closeUserModal = closeUserModal;
    renderUsers();
}
