// src/view.ts
export function initView(API_BASE) {
    const userData = localStorage.getItem("user");
    if (!userData) {
        alert("ÏÑ∏ÏÖò ÎßåÎ£å ÎòêÎäî ÎπÑÏ†ïÏÉÅ Ï†ëÍ∑ºÏûÖÎãàÎã§.");
        window.location.href = "index.html";
        return;
    }
    const user = JSON.parse(userData);
    const userName = document.getElementById("userName");
    const avatar = document.getElementById("avatar");
    if (userName)
        userName.textContent = user.name;
    if (avatar)
        avatar.textContent = user.name.charAt(0).toUpperCase();
    // ‚úÖ ÏÑ∏ÏÖò ÎßåÎ£å Ï≤¥ÌÅ¨ (30Î∂Ñ Í∏∞Ï§Ä)
    const loginTime = user.loginTime;
    const now = Date.now();
    const sessionDuration = 1000 * 60 * 30; // 30Î∂Ñ
    const remainingTime = sessionDuration - (now - loginTime);
    if (remainingTime <= 0) {
        alert("ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏ Ìï¥Ï£ºÏÑ∏Ïöî.");
        localStorage.clear();
        window.location.href = "index.html";
        return;
    }
    // ‚úÖ Ïä§ÌÜ±ÏõåÏπò Ï¥àÍ∏∞Ìôî
    const stopwatchEl = document.getElementById("stopwatch");
    let timeLeft = remainingTime;
    function updateStopwatch() {
        if (!stopwatchEl)
            return;
        const minutes = Math.floor(timeLeft / 60000);
        const seconds = Math.floor((timeLeft % 60000) / 1000);
        stopwatchEl.textContent = `ÎÇ®ÏùÄ ÏãúÍ∞Ñ: ${minutes}:${seconds.toString().padStart(2, "0")}`;
        if (timeLeft <= 0) {
            alert("ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏ Ìï¥Ï£ºÏÑ∏Ïöî.");
            localStorage.clear();
            window.location.href = "index.html";
            return;
        }
        timeLeft -= 1000;
    }
    updateStopwatch();
    const timer = setInterval(updateStopwatch, 1000);
    // ‚úÖ Î°úÍ∑∏ÏïÑÏõÉ Î≤ÑÌäº Ïù¥Î≤§Ìä∏
    document.getElementById("logoutBtn")?.addEventListener("click", () => {
        clearInterval(timer); // ÌÉÄÏù¥Î®∏ Ï†ïÏßÄ
        localStorage.clear();
        fetch(`${API_BASE}/api/login/logout`, {
            method: "POST",
            credentials: "include",
        }).catch(() => { });
        window.location.href = "index.html";
    });
    // ‚úÖ Îí§Î°úÍ∞ÄÍ∏∞ Î∞©ÏßÄ
    history.pushState(null, "", location.href);
    window.onpopstate = function () {
        history.go(1);
    };
    document.getElementById("logoImage")?.addEventListener("click", () => {
        window.location.href = "workspace.html";
    });
}
